import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import { Col, Empty, Modal, Tooltip, Row, Spin } from "antd";
import getLanguage from "../../../App/MyLanguange";

class ModalSearchSupplier extends Component {
  state = {};

  handleCancel = e => {
    this.props.handleModal();
  };

  handleOk = e => {
    this.props.handleModal();
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  render() {

    let columns = [
      {
        title: getLanguage({id: "supplierCode"}),
        dataIndex: "supplier_code",
        rowKey: "supplier_code_readonly",
        width: "40%",
        render: text => (
          <React.Fragment>
            <Tooltip
              placement="bottom"
              title={<IntlMessages id="tooltip.select" />}
            >
              <a
                onClick={e => {
                  e.preventDefault();
                  this.handleSelect(text);
                }}
                href="javascript:;"
                style={{ marginRight: "15px" }}
              >
                {text}
              </a>
            </Tooltip>
          </React.Fragment>
        )
      },
      {
        title: getLanguage({id: "supplierName"}),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "60%"
      }
    ];

    var dataSource = [];
    if (this.props.suppliersData && this.props.idLocation) {
      const suppliersData = this.props.suppliersData;
      // map data dari state global ke dataSource: Table
      Object.keys(suppliersData).map((item, index) => {
        if (this.props.idLocation === suppliersData[item].id_location) {
          return dataSource.push({
            ...suppliersData[item],
            key: item
          });
        }
      });
    }
    const { idLocation, loading } = this.props;
    var loc, locName;
    if (this.props.locations && idLocation) {
      loc = this.props.locations.filter(function (data) {
        return data.id_location === idLocation;
      })[0];
      if (loc) {
        locName = loc.location_name;
      }
    }
    return (
      <Modal
        centered
        footer={null}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        style={{ marginTop: "20px" }}
        title={<IntlMessages id="sidebar.supplier" />}
        visible={this.props.visible}
      >
        <Row>
          <Col>
            {this.props.idLocation ? (
              loading ? (<Spin size="small"></Spin>) : dataSource.length === 0 ? (
                <React.Fragment>
                  <p>
                    <IntlMessages id="priceListAvailableLoc" /> :{" "}
                    <strong>{locName ? locName : ""}</strong>
                  </p>
                  <Empty
                    description={<IntlMessages id="suppliers.table.noData" />}
                  />
                </React.Fragment>
              ) : (
                  <React.Fragment>
                    <p>
                      <IntlMessages id="priceListAvailableLoc" /> :{" "}
                      <strong>{locName ? locName : ""}</strong>
                    </p>
                    <TableWrapper
                      className="invoiceListTable"
                      columns={columns}
                      dataSource={dataSource}
                    />
                  </React.Fragment>
                )
            ) : (
                <Empty
                  description={
                    <IntlMessages id="purchaseOrders.item.noLocation" />
                  }
                />
              )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default ModalSearchSupplier;
