import React, { Component } from "react";
import moment from "moment";

import Button from "../../../../components/uielements/button";
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import ModalRejectSupplierPriceList from "./modalRejectSupplierPriceList";
import ModalSearchSupplier from "./modalSearchSupplier";
import NumberFormat from "../../../App/NumberFormat";
import notification from "../../../../components/notification";
import { customDot } from "../../../App/app.style";

import {
  // Button as ButtonAntd,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  Popconfirm,
  Row,
  Select,
  Spin,
  Steps,
  Tooltip
} from "antd";
import getLanguage from "../../../App/MyLanguange";

const InputGroup = Input.Group;
const Option = Select.Option;
const Step = Steps.Step;
const { RangePicker } = DatePicker;
const { TextArea } = Input;

class SupplierPriceListUpdateForm extends Component {
  state = {
    dataSource: [],
    expired_date: null,
    id_currency: null,
    id_supplier: "",
    id_location: null,
    item: null,
    items: [],
    limit: 0,
    loadingPLSection: false,
    priceListType: null,
    search: null,
    searchSupp: null,
    selected_items: [],
    supplier_name: "",
    start_date: null,
    visibleModalSearchSupplier: false,
    visibleModalRejectSupplierPriceList: false
  };

  componentDidMount() {}

  componentDidUpdate(prevProps) {
    const { limit } = this.state;
    const { supplierPriceList } = this.props.supplierPriceList;
    if (limit === 0 && supplierPriceList !== null) {
      const { supplier_pricelist_details } = supplierPriceList;
      supplier_pricelist_details.map(item => {
        const { selected_items } = this.state;
        return selected_items.push(item.id_item + "_" + item.id_unit);
      });

      let setStateData = {};
      setStateData.id_currency = supplierPriceList.id_currency;
      this.setState(
        {
          ...setStateData,
          dataSource: supplier_pricelist_details,
          id_location: supplierPriceList.id_location,
          limit: 1,
          priceListType: supplierPriceList.pricelist_type
        },
        () => {
          if (supplierPriceList.supplier) {
            this.handleSearchSupplier(
              supplierPriceList.supplier.supplier_name.split(" ")[0]
            );
          }
        }
      );
      this.props.getItemsForSupplierPriceList(supplierPriceList.id_location);
      this.props.getSuppliersBasedOnLocation(supplierPriceList.id_location);
    }
  }

  handleChangeRangePicker = (value, dateString) => {
    this.setState({ start_date: dateString[0], expired_date: dateString[1] });
  };

  handleChangeSelectPriceListType = value => {
    const { id_location } = this.state;
    const priceListType = value;
    const { getSuppliersBasedOnLocation } = this.props;
    if (this.state.priceListType !== value) {
      if (value === 1) {
        getSuppliersBasedOnLocation(id_location);
      }
      this.setState({ priceListType });
      this.props.form.setFieldsValue({
        id_supplier: null,
        id_currency: null
      });
    }
  };

  handleChangeSelectSupplierType = value => {
    this.props.form.setFieldsValue({
      id_supplier_type: value
    });
  };

  handleDataSource = () => {
    const supplier_pricelist_details = this.props.supplierPriceList
      .supplierPriceList.supplier_pricelist_details;
    const dataSource = [];
    for (var i = 0; i < supplier_pricelist_details.length; i++) {
      const newItem = {
        key: supplier_pricelist_details[i].id_supplier_pricelist_detail,
        index_no: i + 1,
        item_code: supplier_pricelist_details[i].item_code,
        id_item: supplier_pricelist_details[i].id_item,
        item_name: supplier_pricelist_details[i].item_name,
        item_price: supplier_pricelist_details[i].item_price,
        discount: supplier_pricelist_details[i].discount
      };

      dataSource.push(newItem);
    }
    // return dataSource;
    this.setState(
      { dataSource },
      this.setState({ donePush: true, dataSource: [] })
    );
  };

  handleDeleteItem = key => {
    var dataSource = [...this.state.dataSource];
    console.log(key, dataSource);
    dataSource = dataSource.filter(item => item.index_no !== key);
    var i = 0;
    let selected_items = [];
    dataSource.map(item => {
      selected_items.push(item.id_item + "_" + item.id_unit);
      i = i + 1;
      item.index_no = i;
    });
    this.setState({
      dataSource,
      selected_items
    });
  };

  handleModalRejectSupplierPriceList = () => {
    this.setState({
      visibleModalRejectSupplierPriceList: !this.state
        .visibleModalRejectSupplierPriceList
    });
  };

  handleModalSearchSupplier = () => {
    const { visibleModalSearchSupplier } = this.state;
    visibleModalSearchSupplier
      ? this.setState({ visibleModalSearchSupplier: false })
      : this.setState({ visibleModalSearchSupplier: true });
  };

  handleLoading = () => {
    if (this.props.supplierPriceList.loading) return true;
    else return false;
  };

  handleRecord = (action, id) => {
    if (action === "confirm") {
      this.props.confirmSupplierPriceList(id);
    } else {
      this.props.rejectSupplierPriceList(id);
    }
  };

  handleSearchSupplier = value => {
    const { id_location } = this.state;
    const { getSuppliersBasedOnLocation } = this.props;
    this.setState({ searchSupp: value });
    getSuppliersBasedOnLocation(id_location, value);
  };

  handleSearchItem = value => {
    const { getItemsForSupplierPriceList } = this.props;
    const { id_location } = this.state;
    if (id_location) {
      this.setState({ search: value });
      getItemsForSupplierPriceList(id_location, value);
    }
  };

  handleSelectSupplier = value => {
    const { id_location, searchSupp } = this.state;
    const { currencies, suppliers } = this.props.supplierPriceList;
    let setStateData = {};

    if (searchSupp) {
      const { getSuppliersBasedOnLocation } = this.props;
      getSuppliersBasedOnLocation(id_location);
      setStateData.searchSupp = null;
    }
    setStateData.id_supplier = value;

    const findSupp = suppliers.find(
      curr_data => curr_data.id_supplier === value
    );
    if (findSupp) {
      const curr = currencies.find(
        data => data.id_currency === findSupp.id_currency
      );
      const id_currency = curr.id_currency;
      this.props.form.setFieldsValue({
        id_currency
      });
    }

    this.setState(setStateData);
  };

  handleSelectItem = value => {
    const { items, id_location } = this.props.supplierPriceList;
    const { search, selected_items } = this.state;
    let idItem = value.slice(0, value.indexOf("_"));
    let idUnit = value.slice(value.indexOf("_") + 1, value.length);
    this.setState({
      item: items.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      )[0]
    });

    if (search && id_location) {
      const { getItemsForSupplierPriceList } = this.props;
      getItemsForSupplierPriceList(id_location);
      this.setState({ search: null });
    }
    // push ke dataSource
    const { dataSource } = this.state;
    const [searchItem] = dataSource.filter(
      item => item.id_item === idItem && item.id_unit === idUnit
    );
    if (searchItem) {
    } else {
      const [newItem] = items.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      );
      const count = dataSource.length + 1;
      this.setState({
        selected_items: [...selected_items, value],
        dataSource: [
          ...this.state.dataSource,
          {
            ...newItem,
            current_price: newItem.last_purchase_price,
            key: newItem.id_item + "_" + newItem.id_unit,
            index_no: count,
            item_price: 0
          }
        ]
      });
    }
  };

  handleSelectLocation = id_location => {
    // const { search } = this.state;
    this.setState({
      id_location,
      dataSource: [],
      search: null,
      selected_items: []
    });
    const { setFieldsValue } = this.props.form;
    // this.setState({  });
    const {
      getItemsForSupplierPriceList,
      getSuppliersBasedOnLocation
    } = this.props;
    getItemsForSupplierPriceList(id_location);
    getSuppliersBasedOnLocation(id_location);
    setFieldsValue({
      id_supplier: null,
      id_currency: null
    });
  };

  handleSelectModal = value => {
    const { currencies, suppliers } = this.props.supplierPriceList;
    const { setFieldsValue } = this.props.form;

    const find_supplier = suppliers.find(
      curr_data => curr_data.supplier_code === value
    );
    if (find_supplier) {
      const curr = currencies.find(
        data => data.id_currency === find_supplier.id_currency
      );
      this.setState({ id_supplier: find_supplier.id_supplier });
      const id_currency = curr.id_currency;
      setFieldsValue(
        {
          id_currency,
          supplier_code: find_supplier.supplier_code,
          supplier_name: find_supplier.supplier_name
        },
        this.handleModalSearchSupplier()
      );
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        value.start_date
          ? (value.start_date = moment(value.start_date, "DD MMMM YYYY").format(
              "YYYY-MM-DD HH:mm:ssss"
            ))
          : (value.start_date = this.props.supplierPriceList.supplierPriceList.start_date);

        value.expired_date
          ? (value.expired_date = moment(
              value.expired_date,
              "DD MMMM YYYY"
            ).format("YYYY-MM-DD HH:mm:ssss"))
          : (value.expired_date = this.props.supplierPriceList.supplierPriceList.expired_date);

        const { dataSource } = this.state;

        value.supplier_pricelist_details = [];

        dataSource.map(item => {
          value.supplier_pricelist_details.push({
            current_price: item.current_price,
            id_item: item.id_item,
            id_unit: item.id_unit,
            item_code: item.item_code,
            item_name: item.item_name,
            discount: item.discount ? item.discount : 0,
            item_price: item.item_price ? item.item_price : 0
          });
        });
        if (value.supplier_pricelist_details.length === 0) {
          notification("error", "Item data can not be empty");
          this.props.clearError();
        } else {
          this.props.submit(value);
        }
      }
    });
  };

  handleUpdateItem = row => {
    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.index_no === item.index_no);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row
    });
    this.setState({ dataSource: newData });
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    // inline style
    var blockDisplay = {
      display: "block"
    };

    // form layout
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { search } = this.state;
    const { getFieldDecorator } = this.props.form;
    const {
      currencies,
      items,
      id_location,
      locations,
      loading,
      loadingItems,
      loadingSubmit,
      loadingSuppliers,
      supplierPriceList,
      suppliers,
      supplier_types
    } = this.props.supplierPriceList;
    const data = supplierPriceList ? supplierPriceList : [];

    // console.log(data);
    const status = () => {
      let curr = 0;
      switch (data.status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 3:
          curr = 2;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* {this.state.donePush === true ? this.handleDataSource() : null} */}
        <ModalSearchSupplier
          handleModal={this.handleModalSearchSupplier}
          handleSelectModal={this.handleSelectModal}
          idLocation={this.state.id_location}
          locations={locations}
          loading={loadingSuppliers}
          suppliersData={suppliers}
          visible={this.state.visibleModalSearchSupplier}
        />
        <ModalRejectSupplierPriceList
          visible={this.state.visibleModalRejectSupplierPriceList}
          handleModal={this.handleModalRejectSupplierPriceList}
          submit={this.props.handleSubmitRejectSupplierPriceList}
          loading={loading}
        />
        <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps size="small" progressDot={customDot} current={status()}>
              <Step title={getLanguage({ id: "new" })} />
              <Step title={getLanguage({ id: "confirmed" })} />
              <Step title={getLanguage({ id: "rejected" })} />
            </Steps>
          </Col>
        </Row>
        {/* Form untuk supplier price list data */}
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <span>
            <IntlMessages id="detail" />{" "}
          </span>
        </Divider>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            {/* Form item untuk pricelist code */}
            <Form.Item
              label={<IntlMessages id="customerPriceLists.priceListCode" />}
            >
              {getFieldDecorator("pricelist_code", {
                initialValue: data.pricelist_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(<Input disabled={data.status !== 1 ? true : false} />)}
            </Form.Item>

            {/* Form item untuk Location */}
            <Form.Item label={<IntlMessages id="sidebar.location" />}>
              {getFieldDecorator("id_location", {
                initialValue: data.id_location,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  filterOption={true}
                  disabled={data.status !== 1 ? true : false}
                  optionFilterProp="children"
                  onChange={this.handleSelectLocation}
                  placeholder={
                    <IntlMessages id="purchaseReceipts.filter.select.location" />
                  }
                  showSearch
                >
                  {locations ? (
                    locations.map(data => (
                      <Option key={data.id_location} value={data.id_location}>
                        {data.location_code} ({data.location_name})
                      </Option>
                    ))
                  ) : (
                    <Option value="" />
                  )}
                </Select>
              )}
            </Form.Item>

            {/* Form item untuk range date */}
            <Form.Item label={<IntlMessages id="priceListRangeDate" />}>
              {getFieldDecorator("range_date", {
                initialValue: [
                  moment(data.start_date, "YYYY-MM-DD"),
                  moment(data.expired_date, "YYYY-MM-DD")
                ],
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <RangePicker
                  allowClear
                  disabled={data.status !== 1 ? true : false}
                  onChange={this.handleChangeRangePicker}
                  format="DD MMMM YYYY"
                  style={{
                    width: "100%"
                  }}
                />
              )}
            </Form.Item>
          </div>

          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            <Row>
              <Col span={9} style={{ paddingRight: "5px" }}>
                {/* Form item untuk pricelist type */}
                <Form.Item
                  label={<IntlMessages id="customerPriceLists.priceListType" />}
                >
                  {getFieldDecorator("pricelist_type", {
                    initialValue: this.state.priceListType,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      disabled={data.status !== 1 ? true : false}
                      onChange={this.handleChangeSelectPriceListType}
                      placeholder={
                        <IntlMessages id="supplierPriceLists.supplierType.select" />
                      }
                    >
                      <Option value={1}>
                        {<IntlMessages id="supplierCode" />}
                      </Option>
                      <Option value={2}>
                        {<IntlMessages id="sidebar.supplierType" />}
                      </Option>
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={15} style={{ paddingLeft: "5px" }}>
                {this.state.priceListType === parseInt(2) ? (
                  // form untuk supplier pricelist type 2
                  <Form.Item label={<IntlMessages id="sidebar.supplierType" />}>
                    {getFieldDecorator("id_supplier_type", {
                      initialValue: data.id_supplier_type,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        disabled={data.status !== 1 ? true : false}
                        onChange={this.handleChangeSelectSupplierType}
                        placeholder={
                          <IntlMessages id="suppliers.filter.select.supplierType" />
                        }
                      >
                        {supplier_types ? (
                          supplier_types.map(item => (
                            <Option
                              key={item.id_supplier_type}
                              value={item.id_supplier_type}
                            >
                              {item.supplier_type_code} (
                              {item.supplier_type_name})
                            </Option>
                          ))
                        ) : (
                          <Option value="" />
                        )}
                      </Select>
                    )}
                  </Form.Item>
                ) : (
                  // form untuk supplier code
                  <Form.Item label={<IntlMessages id="sidebar.supplier" />}>
                    {getFieldDecorator("id_supplier", {
                      initialValue: data.id_supplier,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        // value={searchSupp ? searchSupp : []}
                        showSearch
                        allowClear
                        disabled={data.status !== 1}
                        // optionFilterProp="children"
                        filterOption={false}
                        placeholder={<IntlMessages id="select.supplier" />}
                        loading={loadingSuppliers}
                        notFoundContent={
                          loadingSuppliers ? (
                            <Spin size="small" />
                          ) : (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                        }
                        onSelect={this.handleSelectSupplier}
                        onSearch={this.handleSearchSupplier}
                        style={{ width: "100%" }}
                      >
                        {suppliers && suppliers.length > 0
                          ? suppliers.map(data => (
                              <Option
                                key={data.id_supplier}
                                value={data.id_supplier}
                              >
                                {data.supplier_code} ({data.supplier_name})
                              </Option>
                            ))
                          : null}
                      </Select>
                    )}
                  </Form.Item>
                )}
              </Col>
            </Row>

            <Row>
              <Form.Item
                label={<IntlMessages id="supplierPriceLists.currency" />}
              >
                {getFieldDecorator("id_currency", {
                  initialValue: this.state.id_currency,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    disabled={
                      this.state.priceListType === 1 ||
                      data.status !== 1 ||
                      false
                    }
                    placeholder={<IntlMessages id="select.currency" />}
                  >
                    {currencies ? (
                      currencies.map(data => (
                        <Option key={data.id_currency} value={data.id_currency}>
                          {data.currency_code} ({data.currency_name})
                        </Option>
                      ))
                    ) : (
                      <Option value="" />
                    )}
                  </Select>
                )}
              </Form.Item>
            </Row>

            {/* Form item untuk description */}
            <Form.Item label={<IntlMessages id="suppliers.add.notes" />}>
              {getFieldDecorator("note", {
                initialValue: data.note,
                rules: []
              })(
                <TextArea
                  disabled={data.status !== 1 ? true : false}
                  rows={4}
                />
              )}
            </Form.Item>
          </div>
        </div>

        {/* Form untuk Item Data */}
        <div style={blockDisplay}>
          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="itemDetails" />
          </Divider>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-1 isoFormGroupComp"
              style={{ padding: "0" }}
            >
              <Row style={{ marginBottom: "40px" }}>
                <Col span={24}>
                  <Form.Item {...formItemLayout}>
                    <Select
                      allowClear
                      disabled={data.status !== 1 ? true : false}
                      filterOption={false}
                      showSearch
                      value={search ? search : []}
                      // optionFilterProp="children"
                      placeholder={
                        <IntlMessages id="purchaseOrders.item.placeholder" />
                      }
                      loading={loadingItems}
                      notFoundContent={
                        loadingItems ? (
                          <Spin size="small" />
                        ) : id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={
                              <IntlMessages id="purchaseOrders.item.noLocation" />
                            }
                          />
                        )
                      }
                      onSelect={this.handleSelectItem}
                      onSearch={this.handleSearchItem}
                    >
                      {items.map(data =>
                        !this.state.selected_items.includes(
                          data.id_item + "_" + data.id_unit
                        ) ? (
                          <Option
                            key={data.id_item + "_" + data.id_unit}
                            value={data.id_item + "_" + data.id_unit}
                            style={{ width: "100%" }}
                          >
                            <Row>
                              <Col span={14}>
                                <span>
                                  {data.item_code} ({data.item_name})
                                </span>
                              </Col>
                              <Col span={10}>
                                <span>
                                  {data.unit_name} (
                                  <NumberFormat
                                    value={parseFloat(data.unit_conversion)}
                                  />{" "}
                                  {" " + data.base_unit.unit_name})
                                </span>
                              </Col>
                            </Row>
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <EditableTable
                    dataSource={this.state.dataSource}
                    handleDeleteItem={this.handleDeleteItem}
                    handleUpdateItem={this.handleUpdateItem}
                    status={data.status}
                  />
                </Col>
              </Row>
            </div>
          </div>
        </div>

        {/* Submit Button */}
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
            // style={{ marginLeft: "50px" }}
            loading={loadingSubmit}
            disabled={
              data.status === 2 ||
              data.status === 3 ||
              !permissions.includes("Update Supplier Pricelist")
                ? true
                : false
            }
          >
            <IntlMessages id="button.submit" />
          </Button>
          {/* confirm */}
          <Popconfirm
            title={<IntlMessages id="supplierPriceLists.confirm" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={this.handleRecord.bind(
              this,
              "confirm",
              data.id_supplier_pricelist
            )}
          >
            <Button
              style={{
                marginRight: "5px",
                marginLeft: "10px",
                padding: "0px 15px"
              }}
              disabled={
                data.status === 2 ||
                data.status === 3 ||
                !permissions.includes("Confirm Supplier Pricelist")
                  ? true
                  : false
              }
              // loading={loading || loadingSubmit}
            >
              <IntlMessages id="supplierPriceLists.button.confirm" />
            </Button>
          </Popconfirm>

          {/* cancel */}
          {/* <Popconfirm
            title={<IntlMessages id="supplierPriceLists.reject" />}
            onConfirm={this.handleRecord.bind(
              this,
              "reject",
              data.id_supplier_pricelist
            )}
          /> */}

          <Button
            style={{ marginLeft: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 1 ||
              data.status === 3 ||
              !permissions.includes("Cancel Supplier Pricelist")
                ? true
                : false
            }
            onClick={this.handleModalRejectSupplierPriceList}
            // loading={loading || loadingSubmit}
          >
            <IntlMessages id="supplierPriceLists.button.reject" />
          </Button>
        </Form.Item>

        {/* Form */}
      </Form>
    );
  }
}

const WrappedSupplierPriceListUpdateForm = Form.create({
  name: "update_supplier_price_list"
})(SupplierPriceListUpdateForm);

export default WrappedSupplierPriceListUpdateForm;
