import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";

import actions from "../../../../redux/promotion/supplierPriceList/actions";
import Button from "../../../../components/uielements/button";
import HistorySupplierPriceList from "./historySupplierPriceList";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import notification from "../../../../components/notification";
import PageHeader from "../../../../components/utility/pageHeader";
import SupplierPriceListUpdateForm from "./updateForm";
import { Box } from "../../../App/app.style";
import { Spin, Tooltip } from "antd";
// import { auth } from "../../../../helpers/helper";

const redirecTo = "/dashboard/promotions/supplier-price-lists";

class UpdateSupplierPriceList extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editSupplierPriceList } = this.props;
    const { id } = this.props.match.params;
    editSupplierPriceList(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplierPriceList.error) {
      notification("error", this.props.supplierPriceList.error);
      this.props.clearError();
    }
    if (this.props.supplierPriceList.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.supplierPriceList.success);
      this.props.clearSuccess();
    }
    if (this.props.supplierPriceList.warning) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("warning", this.props.supplierPriceList.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { id } = this.props.match.params;
    const { updateSupplierPriceList } = this.props;
    updateSupplierPriceList(id, value);
  };

  handleSubmitRejectSupplierPriceList = value => {
    const { rejectSupplierPriceList } = this.props;
    const { id } = this.props.match.params;
    // console.log("value dari reject", value);
    value.status = 3;
    rejectSupplierPriceList(id, value);
  };
  render() {
    const { loading, supplierPriceList } = this.props.supplierPriceList;

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="supplierPriceLists.button.update" />}
          >
            <div style={{ alignItems: "center", display: "flex" }}>
              <Tooltip placement="top" title="Info">
                <Button
                  style={{
                    fontSize: "24px",
                    border: 0
                    // color: theme.palette.warning
                  }}
                  icon="issues-close"
                  onClick={() => this.handleModalHistory()}
                  loading={loading}
                >
                  {/* <IntlMessages id="itemRequests.history" /> */}
                </Button>
              </Tooltip>
              <Button
                style={{ marginLeft: "5px" }}
                onClick={() => this.props.history.replace(redirecTo)}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <HistorySupplierPriceList
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={
              supplierPriceList ? supplierPriceList.description : null
            }
          />

          {this.props.supplierPriceList.loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <SupplierPriceListUpdateForm
              confirmSupplierPriceList={this.props.confirmSupplierPriceList}
              supplierPriceList={this.props.supplierPriceList}
              submit={this.handleSubmit}
              getItemsForSupplierPriceList={
                this.props.getItemsForSupplierPriceList
              }
              getSuppliersBasedOnLocation={
                this.props.getSuppliersBasedOnLocation
              }
              handleSubmitRejectSupplierPriceList={
                this.handleSubmitRejectSupplierPriceList
              }
              rejectSupplierPriceList={this.props.rejectSupplierPriceList}
              auth={this.props.auth}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    supplierPriceList: state.supplierPriceList,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSupplierPriceList);
